html, *, :before, :after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, p {
  margin: 0;
}

.calc {
  width: 300px;
  height: 500px;
  color: #fff;
  background-color: #000;
  border: 1px solid #000;
  border-radius: 20px;
  margin: 0 auto;
  padding: 18px;
  font-family: Arial, Helvetica, sans-serif;
  box-shadow: 4px 4px 4px #4f4f4f;
}

.calc-scrin {
  height: 115px;
  flex-direction: row-reverse;
  align-items: flex-end;
  margin-bottom: 16px;
  padding: 10px;
  display: flex;
}

.calc-scrin p {
  font-size: 50px;
}

.buttons {
  flex-wrap: wrap;
  gap: 7px;
  display: flex;
}

.btn {
  width: 60px;
  height: 60px;
  text-align: center;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background-color: #333;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  display: flex;
}

.btn:hover {
  filter: brightness(120%);
}

.btn:active {
  filter: brightness(90%);
}

.btn.bg-gray {
  background-color: #a6a6a6;
}

.btn.bg-orange {
  background-color: #ff9501;
}

.btn.zero {
  width: 127px;
  border-radius: 34px;
}

